/**
 * @description   The function is imported by 'CustomerInfo.vue'
 *  Check if a login user has address 
 *  Get a login user's address if it has
 */

//  import { toRef } from 'vue'
import { reactive } from '@vue/reactivity'
import { get } from '../utils/reqEncap'
// import { checkIsLogin } from './checkIsLogin'
// import { useRoute } from 'vue-router'

     
export function getUserInfo() {
 
    const addrData = reactive({
         hasAddress: false,
         getAddrData: {}
    })
 
    async function getCustomerInfo() {
        const result = await get('/api/user/address/userInfo')
        // console.log(result)
        if(result?.errno === 0 && result.data.length !== 0){
            addrData.getAddrData = result.data[0]       
            // console.log(result.data[0])  
            localStorage.setItem('addrID', addrData.getAddrData._id)
            addrData.hasAddress = true
        }else{
            addrData.hasAddress = false
        }
    }
    return {getCustomerInfo, addrData}
}