<template>
    <div class="specialProductsWrap" >
        <h3> {{productName}} </h3>
        <table class="infoTable" align="center">
            <tr class="categoryTr">
                <td>*</td>
                <td class="dropDown">
                    <!--  -->
                    <button @click="showPicker" >
                        <span class="dropDownTitle">
                            {{title}}
                        </span>
                        <span class="dropDownImg">
                            <img src="img/icons/dropDown.png">
                        </span>
                    </button>
                    <!--  -->
                    <div class="dropDownContents" v-if="showDropDownContents">
                        <!--  -->
                        <div 
                            v-for="item in dropDownList" 
                            :key="item._id" 
                            @click="getCategory(item)"
                        >
                            {{item.name}}
                        </div>
                    </div>
                </td> 
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="updateProductsData.brand"/>
                </td>
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="updateProductsData.name"/>
                </td>
            </tr>
            <tr>
                <td>*</td>
                <td >
                    <input placeholder="" v-model="updateProductsData.quantity" />
                </td>
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="updateProductsData.price" />
                </td>
            </tr>
            <tr>
                <td></td>
                <td>
                    <input placeholder="" v-model="updateProductsData.special" />
                </td>
            </tr>
            <tr class="uploadRow">
                <td>*</td>
                <td>
                    <!-- <el-button @click="dialogVisible = true" type="primary">
                        
                    </el-button> -->
                    <el-button text @click="dialogVisible = true">
                        
                    </el-button>

                    <el-dialog
                        v-model="dialogVisible"
                        title=""
                        width="30%"
                    >
                        <!-- uploadurl -->
                        <span><UploadComponent ref="upload" /></span> 
                        <template #footer>
                        <span class="dialog-footer">
                            <el-button type="primary" @click="dialogVisible = false"></el-button>
                        </span>
                        </template>
                    </el-dialog>
                </td>
            </tr>
            <tr>
                <td></td>
                <td class="imgUrl" v-if="!hasImageUrl">
                    <button @click="uploadNewProduct"></button>
                    <span></span>
                </td>
                <td class="imgUrl" v-if="hasImageUrl">{{this.updateProductsData.imgUrl}}</td>
            </tr>
            <tr>
                <td></td>
                <td>
                    <textarea placeholder="" v-model="updateProductsData.description"/>
                </td>
            </tr>
        </table>
        <div class="buttonWrap">
            <div class="cancelBtn" @click="cancelEvent"></div>
            <div class="confirmBtn" @click="updateProduct"></div>
        </div>
    </div>
    <ToastComponent v-if="toastData.showToast" :message="toastData.toastMessage"/>
</template>

<script>
    import {reactive, toRefs} from '@vue/reactivity'
    import {ref} from 'vue'
    import {getCurrentInstance} from 'vue'
    import {patch} from '../utils/reqEncap'
    import UploadComponent from './UploadComponent.vue'
    import ToastComponent, {ToastParts} from './ToastComponent.vue'

   
        function cancelEvent(cancelBtnEvent) {
                console.log('---', cancelBtnEvent)
                cancelBtnEvent = false
            }

    function handleUpdateProductsEvent(id, name, settingSuccessToast, settingFailureToast) {
        const updateProductsData = reactive({
            brand: '',
            name: '',
            quantity: '',
            price: '',
            special: '',
            imgUrl: '',
            description: '',
        })

        // getCurrentInstancedata(){}
        const productCategory = getCurrentInstance()
        // catagoryValue
        const categoryValue = productCategory.data.category
        // console.log(categoryValue,'---')
        async function updateProduct() {    
            const getID = id._object.productID
            // console.log('name----', name)
            const getName = name._object.productName
            try {            
                const newProduct = await patch(
                    `/api/user/product/${getID}`, {
                        category: productCategory.data.category,
                        brand: updateProductsData.brand,
                        name: updateProductsData.name,
                        quantity: updateProductsData.quantity,
                        price: updateProductsData.price,
                        special: updateProductsData.special,
                        imgUrl: this.updateProductsData.imgUrl,
                        description: updateProductsData.description,                        
                    }
                )
                // console.log('---', newProduct)
                location.reload()
                if(newProduct?.errno === 0){
                    settingSuccessToast(`-${getName}-`)
                }else{
                    settingFailureToast('')
                }
            } catch (error) {
                throw new Error(error)
            }
        }
        return {updateProductsData, updateProduct}
    }

    export default {
        components: {
            UploadComponent, ToastComponent
        },
        props: {
            productID: String,
            productName: String,
            showPanel: Boolean
        },
        data() {
            return {
                title: '',
                dropDownList: [
                    {name: "", category: ''},
                    {name: "", category: ''},
                    {name: "", category: ''},
                    {name: "", category: ''},
                    {name: "", category: ''},
                    {name: "", category: ''},
                    {name: "", category: ''},
                ],
                showDropDownContents: false,
                category: '',
                dialogVisible: false,
                hasImageUrl: false,
            }
        },
        setup(props) {
            // productID
            console.log(props, '---')
            const {productID, productName, showPanel} = toRefs(props)
            const id = productID
            const name = productName
            const cancelBtnEvent = showPanel
            console.log(id,'-----id')
            const {toastData, settingSuccessToast, settingFailureToast} = ToastParts()
            const {
                updateProductsData,  
                getDataCategory, 
                updateProduct
            } = handleUpdateProductsEvent(id, name, settingSuccessToast, settingFailureToast)
            cancelEvent(cancelBtnEvent)

            // console.log(props.productID, '------id')

            return {updateProductsData,  getDataCategory, updateProduct, toastData, cancelEvent}
        },
        methods: {
            // 
            showPicker() {
                this.showDropDownContents = !this.showDropDownContents
            },
            uploadNewProduct() {
                this.updateProductsData.imgUrl = this.$refs.upload.url
                if(this.updateProductsData.imgUrl){
                    this.hasImageUrl = true
                }
            },
            getCategory(item) {
                this.title = item.name
                this.showDropDownContents = !this.showDropDownContents
                this.category = item.category
                console.log(this.category)
                return this.category
            },         
        },
    }

    
</script>

<style lang="scss" >

    .specialProductsWrap {
        position: fixed;
        width: 60%;
        height: 80%;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        background-color: rgba(203, 217, 230, 1);
        border-radius: 5px;
        text-align: center;
        h3 {
            height: 2%;
        }
        .infoTable {
            width: 70%;
            height: 80%;
            tr {
                input {
                    width: 90%;
                }
                td:nth-child(1) {
                    text-align: left;
                    width: 25%;
                }
                td:nth-child(2) {
                    width: 75%;
                }
            }
            .categoryTr {
                .dropDown {
                    position: relative;
                    padding: 0;
                    width: 100%;
                    button {
                        display: flex;
                        margin-left: 50px;
                        padding: 0 auto;
                        padding-left: 20px;
                        width: 35%;
                        height: 36px;
                        background-color: #409eff;
                        color: white;
                        line-height: 33px;
                        border: 1px #d8e6ff solid;
                        border-radius: 5px;
                        .dropDownTitle {
                            width: 125%;
                        }
                        .dropDownImg {
                            width: 30%;
                            height: 100%;
                            // margin-top: 5%;
                            line-height: 41px;
                            margin-left: 20%;
                            padding: 0;
                            img {
                                width: 60%;
                                height: 60%;
                            }
                        }
                    }    
                    .dropDownContents {
                        position: absolute;
                        /* overflow: scroll; */
                        margin-left: 12%;
                        margin-top: 0px;
                        padding: 0;
                        width: 120px;
                        border: 1px grey solid;
                        background-color: #d8e6ff;
                        border-radius: 0 0 15px 15px;
                        div {
                            // list-style: none;
                            font-size: 18px;
                            font-weight: normal;
                            text-align: center;
                            height: 45px;
                            line-height: 45px;
                            transition: all 0.3s ease-out;
                            color: rgb(100, 100, 255);
                        }
                        div:hover {
                            background-color: #409eff;
                            color: white;
                        }
                    }    
                }
            }
            tr .imgUrl {
                font-size: 15px;
                padding: 0;
                button {
                    width: 120px;
                    height: 40px;  
                    margin-left: 20px;                  
                }
                span {
                    font-size: 12px;
                }
            }
            .uploadRow td {
                padding: 0;
                .el-button {
                    background-color: #409eff;
                    margin-left: 0px;
                    padding: 0;
                    color: white;
                    font-size: 17px;
                }
                .el-button:hover {
                    background-color: white;
                    color: #409eff;
                }
                .el-dialog {
                    width: 50%;
                }
            }
            tr td textarea {
                width: 100%;
                height: 90px;
            }
        }
        .buttonWrap {
            display: flex;
            width: 100%;
            height: 5%;
            margin-top: 10px;
            .cancelBtn {
                margin-left: 40%;
                width: 80px;
                height: 30px;
                font-size: 18px;
                background-color: #409eff;
                color: white;
                border-radius: 5px;
                line-height: 30px;
            }
            .cancelBtn:hover {
                background-color: #fff;
                color: #409eff;
                cursor: pointer;
            }
            .confirmBtn {
                margin-left: 30px;
                width: 80px;
                height: 30px;
                font-size: 18px;
                background-color: #409eff;
                color: white;
                border-radius: 5px;
                line-height: 30px;
            }
            .confirmBtn:hover {
                background-color: #fff;
                color: #409eff;
                cursor: pointer;
            }
        }

    }
</style>