<template>
	<div class="productsMngnt">	
		<div class="subSearchWrap">
			<input type="text" placeholder="//">
			<button>
				
			</button>
		</div>	
		<table >
			<caption></caption>
			<thead v-if="checkLoginData.isAdmin">
				<th></th>	
				<th></th>
				<th></th>			
				<th></th>
				<th></th>
				<th></th>	
				<th></th>	
				<th></th>					
				<th></th>	
				<th></th>
				<th></th>
			</thead>
			<tbody v-if="checkLoginData.isAdmin">
				<tr v-for="item in productsList" :key="item._id">
					<td>{{item.name}}</td>
					<td>{{item.category}}</td>
					<td>{{item.brand}}</td>
					<td><div>{{item.imgUrl}}</div></td>
					<td>{{item.quantity}}</td>
					<td>{{item.price}}</td>
					<td>{{item.special}}</td>
					<td><div>{{item.description}}</div></td>
					<td><div>{{item.createdAt}}</div></td>
					<td><button @click="updateProduct(item._id, item.name)"></button></td>
					<td><button @click="deleteProduct(item._id)"></button></td>
				</tr>
			</tbody>
		</table>
		<!-- title:title -->
		<!-- <AdminAddGoods v-if="!createNewProduct"/> -->
	</div>

	<ToastComponent v-if="toastData.showToast" :message="toastData.toastMessage" />
	<MngmntUpdateProduct 
		v-if="updateData.showUploadPanel" 
		:productID="updateData.productID" 
		:productName="updateData.productName"
		:showPanel="updateData.showUploadPanel"
	/>
</template>

<script>
	import {useRouter} from 'vue-router'
	import {reactive, toRefs} from 'vue'
	import {get, patch, deleteItem} from '../../utils/reqEncap'
	import {checkIsLogin} from '../../midwares/checkIsLogin'
	import ToastComponent, {ToastParts} from '../../components/ToastComponent.vue'
	import MngmntUpdateProduct from '../../components/MngmntUpdateProduct.vue'

	function handleProductsEvent() {
		const productsData = reactive({
			productsList: [],
		})

		async function getProducts() {
			const allProducts = await get('/api/user/product')
			productsData.productsList = allProducts.data
			// console.log(productsData.productsList)
		}
		// toRefsdynamic
		const {productsList} = toRefs(productsData)


		return {productsList, getProducts}
	}

	function handleProductsDelete(settingMngmntSuccess, settingMngmntFailure) {
		async function deleteProduct(id) {
			try {
				// console.log(id)
				const deleted = await deleteItem(`/api/user/product/${id}`)
				const productName = deleted.data.name
				location.reload()
				if(productName) {
					settingMngmntSuccess(`${productName}-`)
				}else {
					settingMngmntFailure(`${productName}-`)
				}
			} catch (ex) {
				throw new Error(ex)
			}

		}
		return {deleteProduct}
	}

	function handleProductsUpdate() {
		const updateData = reactive({
			showUploadPanel: false,
			productID: '',
			productName: ''
		})
		function updateProduct(id, name) {
			updateData.productID = id
			updateData.productName = name
			updateData.showUploadPanel = !updateData.showUploadPanel
			// const {productID} = toRefs(updateData)
			// console.log('ID---', updateData.productID)
		}
		return {updateData, updateProduct}
	}
	

	export default {
		name: 'AdminGoods',
		components: {ToastComponent, MngmntUpdateProduct},
		setup () {
			const {toastData, settingMngmntSuccess, settingMngmntFailure} = ToastParts()
			const {productsList, getProducts} = handleProductsEvent()
			const {updateData, updateProduct} = handleProductsUpdate()
			const {deleteProduct} = handleProductsDelete(settingMngmntSuccess, settingMngmntFailure)
			const {checkLoginData} = checkIsLogin()
			if(checkLoginData.isAdmin){
				getProducts()
			}			

			// deleteProduct(productID)
			return {
				productsList, 
				getProducts, 
				deleteProduct, 
				checkLoginData, 
				toastData,
				updateProduct,
				updateData
			}
		}
	}
</script>

<style lang="scss">
	.productsMngnt {	
		.subSearchWrap {
			margin: 50px auto;
			padding: 0 auto;
			text-align: center;
			display: flex;
			input{
				margin-left: 25%;
				margin-right: 1%;
				width: 36%;
				height: 29px;
			}
			button{
				width: 90px;
				height: 35px;
				margin-left: 10px;
			}
		}
		table {
			/* margin-left: 8%; */
			margin: 0 auto;
			padding: 10px;
			width: 100%;
			// font-size: 20px;
			caption {
				width: 100%;
				margin-bottom: 50px;
				font-size: 30px;
				font-weight: bold;
			}
			thead th {
				border-bottom: 1px grey solid;
				border-right: 1px grey solid;
				// min-width: 100px;
				font-size: 18px;
				white-space: nowrap; // 
			}
			tbody {
				tr {
					td {
						padding: 0;
						border-bottom: 1px grey solid;
						border-right: 1px grey solid;
						width: 30px;
						height: 30px;
						text-align: center;
						white-space: nowrap; // 
						font-size: 15px;
						div {
							width: 28px;
							height: 28px;
							overflow: hidden;
							text-overflow: ellipsis;
							text-align: center;
							line-height: 45px;
						}
					}
				}
				tr:hover{
					background-color: #cfe0fe;
				}
			}
		}
	}	
</style>