<template>

	<!-------------------------------------Border line of gallery----------------------------------->
	<div class="borderLine">
		<div class="left">
			<img alt="" src="img/border line.png">
		</div>
		<div class="centerTitle"></div>
		<div class="right">
			<img alt="" src="img/border line.png">
		</div>
		<div class="more">
			<a href="" >+</a>
		</div>

	</div>	
	
				
	<!-----------------------------------------Honey gallery------------------------------------------>
	
	<div class="honeyProductsGallery">		
		<div class="honeys" v-for="item in mostSaleHoney.slice(0, 10)" :key="item._id">
			<div class="picture">
				<img src="" alt="">
			</div>
			<div class="content">
				<div class="name">{{item.name}}</div>
				<div class="priceSection">{{item.price}}</div>
			</div>

		</div>
	</div>
</template>

<script>
	import {reactive, toRefs} from '@vue/reactivity'
	import {get} from '../../utils/reqEncap'
	
	function handleHoneyDisplayEvent() {
		const reactiveData = reactive({
			mostSaleHoney: []
		})

		async function getMostSaleHoneys() {
			const getData = await get('/api/user/product')
			reactiveData.mostSaleHoney = getData.data.filter(function(obj){
				return obj.category == ''
			})
			
		}
		const {mostSaleHoney} = toRefs(reactiveData)

		return {mostSaleHoney, getMostSaleHoneys}
	}

	export default {
		name: 'HomeHoneyDisplay',
		setup() {
			const {mostSaleHoney, getMostSaleHoneys} = handleHoneyDisplayEvent()
			getMostSaleHoneys()
		
			return {mostSaleHoney, getMostSaleHoneys}
		}
	}
</script>

<style lang="scss">
	.honeyProductsGallery{
		margin: 0 auto;
		padding: 0;
		margin-left: 10%;
		width: 80%;
		// display: inline-block;     /*-Subdivs in row-*/
		// flex-flow: row wrap;     /*-Change extra subdivs to next row-*/
		.honeys {
			float: left;
			margin: 1%;
			padding: 0;
			width: 17%;
			height: 290px;
			border: 1px solid grey;
			text-align: center;
			line-height: 350px;
			.picture {
				width: 80%;
				height: 70%;
				/* images set in mid postion */
				display: block;
				margin: 0 auto;
				background-color: #cfe0fe;
				img {
					width: 100%;
					height: 100%;
				}
			}
			.content {
				height: 30%;
				.name{
					height: 45%;
					line-height: 300%;
					font-size: 100%;
					font-weight: bold;
					border: 1px solid grey;
					color: #3a1e9d;
				}
				.priceSection{
						height: 50%;
						// border: none;
						line-height: 300%;
						font-size: 100%;
						font-weight: bold;
						color: rgb(190, 3, 3);
				}
			}

		}
	}
</style>