<template>
	<div class="mngntWrap">
		<div  class="leftTitleTags">
			<div 
				v-for="(item, index) in titleMngntTags" 
				:key="item.id" 
				@click="handleTagsEvent(index)"
				:class="{isActive: active===index}"
			>
				{{item}}
			</div>
		</div>
		<div class="rightContentSections">            
            <HeaderComponent />
			<AdminCustomers v-if="active===0" />
			<AdminGoods v-if="active===1" />
			<AdminAddNewGood v-if="active===2" />
		</div>
	</div>
</template>

<script>
    import {ref, nextTick, provide} from 'vue'
	import {useRouter} from 'vue-router'
	import {reactive} from '@vue/reactivity'
	import HeaderComponent from '../../components/HeaderComponent.vue'
	import AdminCustomers from './AdminCustomers.vue'
	import AdminGoods from './AdminGoods.vue'
	import AdminAddNewGood from './AdminAddNewGood.vue'

    export default {
        name: 'AdminMngnt',
        components: {
			HeaderComponent, 
			// AdminMngntFruit, 
			// AdminMngntSpecial, 
			// AdminMngntNew,
			AdminCustomers,
			AdminGoods,
			AdminAddNewGood
		}, 
        data() {
			return {
				titleMngntTags: [
					"",
					"",
					""								
				],
				active: 0,
			}	
        },
		methods: {
			handleTagsEvent(index) {
				this.active = index
			}
		}
    }

</script>

<style scoped>
	/*--------Management Wrap---------*/
	.mngntWrap {
		 /* To let right part scroll, it can't be display:flex, it should be 'position:relative' instead */
		display: flex;
		width: 100%;
		margin: 0 auto;
		padding: 3px;
        position: relative;
	}

	/* ---Special Section--- */
	.mngntWrap .leftTitleTags {
		width: 12%;
		margin-left: 0;
        padding-top: 50px;
        position: fixed;
        top: 0;
        bottom: 0;
        background-color: rgb(234, 234, 234);
	}

	.mngntWrap .leftTitleTags div {
		width: 100%;
		height: 55px;
		list-style: none;
		margin: 1px;
		cursor: pointer;
		text-align: center;
		line-height: 60px;
		font-size: 20px;
		background-color: rgb(234, 234, 234);
	}

	.mngntWrap .leftTitleTags .isActive {
		background-color: rgb(246, 246, 246);
	}

	.mngntWrap .rightContentSections {
		width: 87%;
		margin-left: 12.5%;
        height: 1200px;
	}


</style>