<template>
	<!-----------------------------------Header---------------------------------->
	<HeaderComponent />

  	<!--------------------------Company Icon & Search Bar------------------------>
	<SearchComponent />

  	<!---------------------------------Navigator-------------------------------->
	<NavComponent />
    <div class="titleInCart"> </div>
    <div class="notLoginContent" v-if="checkLoginData.isNotLogin">
          
    </div>
    <div class="orderWrapArea" v-else-if="!checkLoginData.isNotLogin && !addrData.hasAddress">
        
    </div>
    <div class="orderWrapArea" v-else>
        <div class="subTitle"></div>
        <div class="addrDetails">
            {{addrData.getAddrData.province}} / 
            {{addrData.getAddrData.city}} / 
            {{addrData.getAddrData.suburb}} / 
            {{addrData.getAddrData.street}} / 
            {{addrData.getAddrData.houseNum}} / 
            {{addrData.getAddrData.province}} / 
             {{addrData.getAddrData.phone}} / 
             {{addrData.getAddrData.name}}
        </div>
    </div>
</template>

<script>
	import HeaderComponent from '../../components/HeaderComponent.vue'
	import SearchComponent from '../../components/SearchComponent.vue'
	import NavComponent from '../../components/NavComponent.vue'
    import {checkIsLogin} from '../../midwares/checkIsLogin'
    import {getUserInfo} from '../../midwares/checkHasAddress'


    export default {
        name: 'CustomerOrder',
        components: {HeaderComponent, SearchComponent, NavComponent},
        setup() {
            const {checkLoginData} = checkIsLogin()
            const {getCustomerInfo, addrData} = getUserInfo()
            if(!checkLoginData.isNotLogin) {
                getCustomerInfo()
            }

            return {checkLoginData, getCustomerInfo, addrData}
        }
    }
</script>

<style lang="scss">

    .titleInCart {
        margin: 20px auto;
        font-size: 30px;
        font-weight: bold;
        text-align: center;
    }

    .notLoginContent {
        margin: 0 auto;
        margin-top: 150px;
        text-align: center;
        font-size: 30px;
        font-weight: bold;
        color: #9ec1fc
    }

    .orderWrapArea {
        margin: 0 auto;
        margin-top: 10px;
        width: 80%;
        text-align: center;
        .subTitle {
            margin: 0 auto;
            height: 50px;
            font-size: 20px;
            font-weight: bold;
            text-align: center;
            line-height: 50px;
            background-color: #e4eeff;
        }
        .addrDetails {
            text-align: center;
            height: 50px;
            line-height: 50px;
            font-size: 18px;
            background-color: #e4eeff;
        }
    }
</style>