<template>
	<!-----------------------------------Header wrap---------------------------------->
	<HeaderComponent />
	
	<!--------------------------Company Icon & Search Bar wrap------------------------>
	<SearchComponent />
	
	<!---------------------------------Navigator wrap-------------------------------->
	<NavComponent />


	<!--------------------------------Products catalog--------------------------->
	<div class="productsArea">
        <SideProductsCatlogComponent />
        
		<!--  Show the div part when creating new address or change old address -->
        <div class="customerInfoWrap" >
            <table class="infoTable" align="center">
                <!-- <tr>
                    <td></td> -->
                    <!-- When user is not login -->
                    <!-- <td></td>
                </tr>						          -->
                <tr>
                    <td>*</td>
                    <td>
                        <input placeholder="" v-model="newAddrData.province" />
                    </td> 
                </tr>
                <tr>
                    <td>*</td>
                    <td>
                        <input placeholder="" v-model="newAddrData.city" />
                    </td>
                </tr>
                <tr>
                    <td>/ *</td>
                    <td>
                        <input placeholder="/ " v-model="newAddrData.suburb" />
                    </td>
                </tr>
                <tr>
                    <td>/ *</td>
                    <td >
                        <input placeholder="/ " v-model="newAddrData.street" />
                    </td>
                </tr>
                <tr>
                    <td>*</td>
                    <td>
                        <input placeholder="  " v-model="newAddrData.houseNum" />
                    </td>
                </tr>
                <tr>
                    <td>*</td>
                    <td>
                        <input placeholder="" v-model="newAddrData.name" />
                    </td>
                </tr>
                <tr>
                    <td>*</td>
                    <td>
                        <input placeholder="" v-model="newAddrData.phone" />
                    </td>
                </tr>
            </table>
            <button class="createAddr" @click="addNewAddr" >
                
            </button>
        </div>
    </div>
    <ToastComponent v-if="toastData.showToast" :message="toastData.toastMessage"/>
</template>

<script>

    import { reactive } from '@vue/reactivity'
    // import { useRouter } from 'vue-router'
	import HeaderComponent from '../../components/HeaderComponent.vue'
	import SearchComponent from '../../components/SearchComponent.vue'
	import NavComponent from '../../components/NavComponent.vue'
	import SideProductsCatlogComponent from '@/components/SideProductsCatlogComponent.vue'
    // import {get} from '../../utils/reqEncap'
    import {checkIsLogin} from '../../midwares/checkIsLogin'
    import {getUserInfo} from '../../midwares/checkHasAddress'
    import {post} from '../../utils/reqEncap'
    import ToastComponent, {ToastParts} from '../../components/ToastComponent.vue'



    // Create receiving address as required
    function handleNewAddress(settingSuccessToast, settingFailureToast) {

        const newAddrData = reactive({
            province: '',
            city: '',
            suburb: '',
            street: '', 
            houseNum: '',
            name: '',
            phone: '',
            vip: false
        })

        async function addNewAddr() {
            if(newAddrData.province.trim() === '' ){
                settingFailureToast('')
            }else if(newAddrData.city.trim() === '' ) {
                settingFailureToast('')
            }else if(newAddrData.suburb.trim() === '' ) {
                settingFailureToast('/')
            }else if(newAddrData.street.trim() === '' ) {
                settingFailureToast('/')
            }else if(newAddrData.houseNum.trim() === '' ) {
                settingFailureToast('')
            }else if(newAddrData.name.trim() === '' ) {
                settingFailureToast('')
            }else if(newAddrData.phone.trim() === '' ) {
                settingFailureToast('')
            }else {
                try{
                    const addingNewAddrResult = await post(
                        '/api/user/address', {
                            province: newAddrData.province,
                            city: newAddrData.city,
                            suburb: newAddrData.suburb,
                            street: newAddrData.street, 
                            houseNum: newAddrData.houseNum,
                            name: newAddrData.name,
                            phone: newAddrData.phone,
                            vip: false
                    })
                    console.log(addingNewAddrResult)
                    if(addingNewAddrResult?.errno === 0) {
                        settingSuccessToast('')
                    }else{
                        settingFailureToast('')
                    }
                }catch(ex) {
                    throw new Error(ex)
                }
            }
        }
        return {newAddrData, addNewAddr}
    }

    export default {
        name: 'CreateAddress',
        components: {HeaderComponent, SearchComponent, NavComponent, SideProductsCatlogComponent, ToastComponent}, 
        setup() {
            const {checkLoginData} = checkIsLogin()
            const {addressInfo, getCustomerInfo, addrData} = getUserInfo()
            const {toastData, settingSuccessToast, settingFailureToast} = ToastParts()
            const {newAddrData, addNewAddr} = handleNewAddress(settingSuccessToast, settingFailureToast)

            if(checkLoginData.isNotLogin == false){
                    getCustomerInfo()
            }
                            
            return { 
                checkLoginData, 
                addressInfo, 
                addrData, 
                newAddrData, 
                toastData, 
                addNewAddr
            }
        }
    }
</script>

<style scoped>



</style>