<template>
    <!-----------------------------------Header wrap---------------------------------->
	<HeaderComponent />
	
	<!--------------------------Company Icon & Search Bar wrap------------------------>
	<SearchComponent />
		
	<!---------------------------------Navigator wrap-------------------------------->
	<NavComponent />


	<div v-if="reactiveData.hasSeasonFruits" class="titleWrap">
		<div class="title">
			<div class="left">	</div>
			<div class="centerTitle"></div>
			<div class="right">	</div>
		</div>	

		<div class="fruitProducts" v-for="item in fruitProducts" :key="item._id">
			<div class="special" @click="routeToSingleProduct(item._id)">
				<img src="item.imgUrl" />
				<div class="content">
					<div class="name">{{item.name}}</div>
				</div>
			</div>
		</div>
	</div>
	<marquee v-else class="noFruitProducts" scrollamount=25></marquee>
</template>

<script>
	import { reactive, toRefs } from '@vue/reactivity'
	import {useRouter} from 'vue-router'
	import {get} from '../../utils/reqEncap'
    import HeaderComponent from '../../components/HeaderComponent.vue'
    import SearchComponent from '../../components/SearchComponent.vue'
    import NavComponent from '../../components/NavComponent.vue'

	function handleGetFruitsEvent() {
		const reactiveData = reactive({
			fruitProducts: [],
			hasSeasonFruits: false
		})
		const route = useRouter()

		async function getFruitsProducts() {
			const fruitProductsData = await get('/api/user/product')
			// console.log(fruitProductsData.data)
			// reactive
			// 
			reactiveData.fruitProducts = fruitProductsData.data.filter(
				function(obj){
					return obj.category == '' 
				}
			)
			if(reactiveData.fruitProducts.length == 0 ) {
				reactiveData.hasSeasonFruits = false
			}else{
				reactiveData.hasSeasonFruits = true
			}
		}

		function routeToSingleProduct (singleProductId) {
			localStorage.setItem('singleProductId', singleProductId);
			route.push({name: 'SingleProduct'});
		}
		const {fruitProducts} = toRefs(reactiveData)
		
		return {getFruitsProducts, fruitProducts, reactiveData, routeToSingleProduct}
	}

	export default {
		name: 'fruitsArea',
		components: {HeaderComponent, SearchComponent, NavComponent},
		setup() {
			const {getFruitsProducts, fruitProducts, reactiveData, routeToSingleProduct} = handleGetFruitsEvent()
			getFruitsProducts()

			return {getFruitsProducts, fruitProducts, reactiveData, routeToSingleProduct}
		}
	}
</script>

<style lang="scss">
	/*-Div of border line-*/
	.titleWrap {
		margin-left: 11%;
		width: 75%;    
		.title{  		
			margin-left: 10%;
			padding: 0;
			width: 100%;    
			height: 80px;
			line-height: 80PX;
			text-align: center;
			display: flex;     /*-Subdivs in row-*/
			flex-flow: nowrap;     /*-No change extra subdivs to next row-*/
			.centerTitle{
				font-size: 150%;
				font-weight: bold;
				width: 20%;
			}
			.left{
				width: 30%;
			}
			.right{

				width: 30%;
			}
		}
	}



/*------------------------------------------- Products on special --------------------------------------*/

	.fruitProducts{
		margin: 0 auto;
		padding: 0;
		margin-left: 0px;
		width: 100%;
		border: 1px red;
		.special{
			float: left;
			margin: 1%;
			padding: 0;
			width: 31%;
			height: 390px;
			border: 1px solid grey;
			text-align: center;
			line-height: 350px;
			img{
				width: 80%;
				height: 70%;
				/* images set in mid postion */
				display: block;
				margin: 0 auto;
				border: 1px solid red;
			}
			.content{
				height: 30%;
				.name{
					height: 45%;
					line-height: 50px;
					font-size: 22px;
					border: 1px solid grey;
				}
				.priceSection{
					height: 50%;
				}
			}
		}
		.special:hover {
			cursor: pointer;
		}
	}

	.noFruitProducts {
		margin: 150px auto;
		margin-left: 12%;
		width: 80%;
		text-align: center;
		font-size: 200%;
		font-weight: bold;
		font-style: italic;
		color: #0658df;
	}

</style>