<template>
    <div class="customerInfoMngmnt" >
        <h3> {{userName}} </h3>
        <table class="infoTable" align="center">
            <!-- <tr>
                <td></td> -->
                <!-- When user is not login -->
                <!-- <td></td>
            </tr> -->
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="updateAddrData.province" />
                </td> 
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="updateAddrData.city" />
                </td>
            </tr>
            <tr>
                <td>/ *</td>
                <td>
                    <input placeholder="/ " v-model="updateAddrData.suburb" />
                </td>
            </tr>
            <tr>
                <td>/ *</td>
                <td >
                    <input placeholder="/ " v-model="updateAddrData.street" />
                </td>
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="  " v-model="updateAddrData.houseNum" />
                </td>
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="updateAddrData.name" />
                </td>
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="updateAddrData.phone" />
                </td>
            </tr>
            <tr>
                <td>vip*</td>
                <td>
                    <input placeholder="vip" v-model="updateAddrData.vip" />
                </td>
            </tr>
        </table>
        <button class="createAddr" @click="updateAddr()" >
            
        </button>
    </div>
    <ToastComponent v-if="toastData.showToast" :message="toastData.toastMessage"  />
</template>

<script>
    import {reactive, toRefs} from '@vue/reactivity'
    import {getCurrentInstance} from 'vue'
    import {patch} from '../utils/reqEncap'
    import ToastComponent, {ToastParts} from './ToastComponent.vue'

            function handleUpdateAddrEvent(id, name, settingSuccessToast, settingFailureToast) {
                const updateAddrData = reactive({
                    province: '',
                    city: '',
                    suburb: '',
                    street: '',
                    houseNum: '',
                    phone: '',
                    name: '',
                    vip: false
                })
                async function updateAddr() {    
                    // console.log('object----', id._object)
                    const getID = id._object.userID
                    const getName = name._object.userName
                    // console.log('getName----', getName)
                    if(updateAddrData.province.trim() === '' ){
                        settingFailureToast('')
                    }else if(updateAddrData.city.trim() === '' ) {
                        settingFailureToast('')
                    }else if(updateAddrData.suburb.trim() === '' ) {
                        settingFailureToast('/')
                    }else if(updateAddrData.street.trim() === '' ) {
                        settingFailureToast('/')
                    }else if(updateAddrData.houseNum.trim() === '' ) {
                        settingFailureToast('')
                    }else if(updateAddrData.name.trim() === '' ) {
                        settingFailureToast('')
                    }else if(updateAddrData.phone.trim() === '' ) {
                        settingFailureToast('')
                    }else {
                        try {            
                            const updated = await patch(
                                `/api/user/address/management/${getID}`, {
                                    username: getName,
                                    province: updateAddrData.province,
                                    city: updateAddrData.city,
                                    suburb: updateAddrData.suburb,
                                    street: updateAddrData.street,
                                    houseNum: updateAddrData.houseNum,
                                    name: updateAddrData.name,
                                    phone: updateAddrData.phone,    
                                    vip: updateAddrData.vip                    
                                }
                            )
                            console.log('---', updated)
                            location.reload()
                            if(updated?.errno === 0){
                                settingSuccessToast(`${getName}`)
                            }else{
                                settingFailureToast('')
                            }
                        } catch (error) {
                            throw new Error(error)
                        }
                    }
                }
                return {updateAddrData, updateAddr}
            }

    export default {
        components: {
            ToastComponent
        },
        // 3. Child compnt receives data from parent by using props
        props: {
            userID: String,
            userName: String
        },
        // 4. the function setup(){} using data by put it into params, so that child functions would be able to use the data in props
        setup(props) {
            // productIDproductIDstatic data
            // ReferenceError: updateProductsData is not defined
            console.log(props, '---ID')
            const {userID, userName} = toRefs(props)
            const id = userID
            const name = userName
            // console.log(id,'-----id')
            const {toastData, settingSuccessToast, settingFailureToast} = ToastParts()
            const {updateAddrData, updateAddr} = handleUpdateAddrEvent(id, name, settingSuccessToast, settingFailureToast)
            // // console.log(props.productID, '------id')

            return {
                updateAddrData, 
                updateAddr,
                toastData, 
            }
        }
    }

    
</script>

<style lang="scss" >
    .customerInfoMngmnt {
        position: fixed;
        width: 60%;
        height: 80%;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        background-color: rgba(203, 217, 230, 1);
        border-radius: 5px;
        text-align: center;
        .infoTable {
            width: 60%;
            height: 80%;
        }
        button {
            margin-top: 10px;
            margin-left: 30px;
            width: 100px;
            height: 35px;
            font-size: 18px;
            background-color: #409eff;
            color: white;
            border-radius: 5px;
            line-height: 30px;
        }
        button:hover {
            background-color: #fff;
            color: #409eff;
            cursor: pointer;
        }
    }
</style>