<template>
    <div class="newProductsWrap">
        <table class="infoTable" align="center">
            <tr class="categoryTr">
                <td>*</td>
                <td class="dropDown">
                    <button @click="showPicker" >
                        <span class="dropDownTitle">
                            {{title}}
                        </span>
                        <span class="dropDownIcon">
                            <img src="img/icons/dropDown.png">
                        </span>
                    </button>
                    <div class="dropDownContents" v-if="showDropDownContents">
                        <div v-for="item in dropDownList" :key="item._id" @click="getCategory(item)">{{item.name}}</div>
                    </div>
                </td> 
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="newProductsData.brand"/>
                </td>
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="newProductsData.name"/>
                </td>
            </tr>
            <tr>
                <td>*</td>
                <td >
                    <input placeholder="" v-model="newProductsData.quantity" />
                </td>
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="newProductsData.price" />
                </td>
            </tr>
            <tr>
                <td>*</td>
                <td>
                    <input placeholder="" v-model="newProductsData.special" />
                </td>
            </tr>
            <tr class="uploadRow">
                <td>*</td>
                <td>
                    <!-- <el-button @click="dialogVisible = true" type="primary">
                        
                    </el-button> -->
                    <el-button text @click="dialogVisible = true">
                        
                    </el-button>

                    <el-dialog
                        v-model="dialogVisible"
                        title=""
                        width="30%"
                    >
                        <!-- url -->
                        <span><UploadComponent ref="upload" /></span>
                        <template #footer>
                        <span class="dialog-footer">
                            <el-button type="primary" @click="dialogVisible = false"
                            ></el-button
                            >
                        </span>
                        </template>
                    </el-dialog>
                </td>
            </tr>
            <tr>
                <td></td>
                <td class="imgUrl" v-if="!hasImageUrl">
                    <button @click="uploadNewProduct"></button>
                    <span></span>
                </td>
                <td class="imgUrl" v-if="hasImageUrl">{{this.newProductsData.imgUrl}}</td>
            </tr>
            <tr>
                <td></td>
                <td>
                    <textarea placeholder="" v-model="newProductsData.description"/>
                </td>
            </tr>
        </table>
    </div>
    <div class="buttonsWrap">
        <button @click="createNewProduct"></button>
    </div>
    <ToastComponent v-if="toastData.showToast" :message="toastData.toastMessage"/>
</template>

<script>
    import {reactive, toRefs} from '@vue/reactivity'
    import {ref} from 'vue'
    import {getCurrentInstance} from 'vue'
    import UploadComponent from '../../components/UploadComponent.vue'
    import {post} from '../../utils/reqEncap'
    import ToastComponent, {ToastParts} from '../../components/ToastComponent.vue'

    function handleCreateProduct(settingSuccessToast, settingFailureToast) {
        const newProductsData = reactive({
            brand: '',
            name: '',
            quantity: '',
            price: '',
            special: '',
            imgUrl: '',
            description: '',
        })

        const productCategory = getCurrentInstance()
        // catagoryValue
        const categoryValue = productCategory.data.category
        // console.log(categoryValue,'---')
        async function createNewProduct() {    
            try {            
                const newProduct = await post(
                    '/api/user/product', {
                        category: productCategory.data.category,
                        brand: newProductsData.brand,
                        name: newProductsData.name,
                        quantity: newProductsData.quantity,
                        price: newProductsData.price,
                        special: newProductsData.special,
                        imgUrl: this.newProductsData.imgUrl,
                        description: newProductsData.description,                        
                    }
                )
                console.log('---', newProduct)
                location.reload()
                if(newProduct?.errno === 0){
                    settingSuccessToast('')
                }else{
                    settingFailureToast('')
                }
            } catch (error) {
                throw new Error(error)
            }
        }
        return {newProductsData, createNewProduct}
    }

    export default {
        components: {
            UploadComponent, ToastComponent
        },
        data() {
            return {
                title: '',
                dropDownList: [
                    {name: "", category: ''},
                    {name: "", category: ''},
                    {name: "", category: ''},
                    {name: "", category: ''},
                    {name: "", category: ''},
                    {name: "", category: ''},
                    {name: "", category: ''},
                ],
                showDropDownContents: false,
                category: '',
                dialogVisible: false,
                hasImageUrl: false
            }
        },
        setup() {
            const {toastData, settingSuccessToast, settingFailureToast} = ToastParts()
            const {newProductsData, createNewProduct} = handleCreateProduct(settingSuccessToast, settingFailureToast)

            return {newProductsData, createNewProduct, toastData}
        },
        methods: {
            // 
            showPicker() {
                this.showDropDownContents = !this.showDropDownContents
            },
            //     
            getCategory(item) {
                this.title = item.name
                this.showDropDownContents = !this.showDropDownContents
                this.category = item.category
                // console.log(this.category)
            },
            // url
            uploadNewProduct() {
                this.newProductsData.imgUrl = this.$refs.upload.url
                if(this.newProductsData.imgUrl){
                    this.hasImageUrl = true
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .newProductsWrap {
        width: 90%;
        /* height: 8%; */
        margin-left: 5%;
        margin-top: 3%;
        /* transform: translate(-50%, -50%); */
        background-color: white;
        padding: 10px;
        border-radius: 5px;
        color: black;
        font-size: 20px;
        font-weight: bold;
        text-align: center;
        line-height: 50px;
        .infoTable {
            width: 70%;
            tr {
                td:nth-child(2) {
                    padding: 0;
                    width: 80%;
                    text-align: left;
                    input#file-input{
                        display: none;
                    }
                }
                td:nth-child(1) {
                    text-align: left;
                }
            }
            tr td .is-text {
                background-color: rgb(238, 238, 238);
                margin-left: 30px;
                width: 110px;
                height: 40px;
                font-size: 15px;
                font-weight: bold;
                color: #409eff;
            }
            tr td .is-text:hover {
                background-color: #409eff;
                color: white;
            }
            .categoryTr .dropDown {
                position: relative;
                padding: 0;
                button {
                    display: flex;
                    margin-left: 20px;
                    padding: 0 auto;
                    padding-left: 20px;
                    width: 35%;
                    height: 36px;
                    background-color: #409eff;
                    color: white;
                    line-height: 33px;
                    border-radius: 5px;
                    .dropDownTitle{
                        width: 65%;
                    }
                    .dropDownIcon {
                        width: 30%;
                        height: 100%;
                        margin-left: 20%;
                        padding: 0;
                        line-height: 41px;
                        img {
                            width: 50%;
                            height: 80%;
                        }
                    }
                }
                button:hover {
                    background-color: #0861c1;
                }
                .dropDownContents {
                    position: absolute;
                    /* overflow: scroll; */
                    margin-left: 3%;
                    margin-top: 0px;
                    padding: 0;
                    width: 120px;
                    border: 1px grey solid;
                    background-color: #d8e6ff;
                    border-radius: 0 0 15px 15px;
                    div {
                        // list-style: none;
                        font-size: 18px;
                        font-weight: normal;
                        text-align: center;
                        height: 45px;
                        line-height: 45px;
                        transition: all 0.3s ease-out;
                        color: rgb(100, 100, 255);
                    }
                    div:hover {
                        background-color: #409eff;
                        color: white;
                    }
                }                
            }
            tr .imgUrl {
                font-size: 15px;
                button {
                    height: 40px;                    
                }
                span {
                    margin-left: 30px;
                }
            }
            tr td textarea {
                width: 70%;
                height: 200px;
            }
        }
    }

    .buttonsWrap button {
        width: 80px;
        height: 40px;
        float: right;
        margin-right: 200px;
    }
</style>