<template>
    <div class="productsCatalog">		
        <ul class="productsNav">
            <li @click="routerLinkToMilkPowder"></li>
            <li @click="routerLinkToFeminine"></li>
            <li @click="routerLinkToNutrition"></li>
            <li @click="routerLinkToCommodity"></li>
            <li @click="routerLinkToSnack"></li>
            <li @click="routerLinkToBabySupplement"></li>
        </ul>	
    </div>
</template>

<script>
	import {useRouter} from 'vue-router'

	const handleRouterLinkEvent = () => {

		const router = useRouter()
        
		function routerLinkToMilkPowder() {
			router.push({name: 'MilkPowders'})
		}
        function routerLinkToNutrition() {
            router.push({name: 'NutritionProducts'})
        }
        function routerLinkToFeminine() {
            router.push({name: 'FeminineProducts'})
        }
        function routerLinkToCommodity() {
            router.push({name: 'CommodityProducts'})
        }
        function routerLinkToSnack() {
            router.push({name: 'SnackProducts'})
        }
        function routerLinkToBabySupplement() {
            router.push({name: 'BabySupplements'})
        }
		return { 
            routerLinkToMilkPowder, 
            routerLinkToNutrition, 
            routerLinkToFeminine, 
            routerLinkToCommodity, 
            routerLinkToSnack, 
            routerLinkToBabySupplement 
        }
	}

    export default {
        name: 'NavComponent',
		setup() {
			const { 
                routerLinkToMilkPowder, 
                routerLinkToNutrition, 
                routerLinkToFeminine, 
                routerLinkToCommodity, 
                routerLinkToSnack,
                routerLinkToBabySupplement 
            } 
            = handleRouterLinkEvent()
			return { 
                routerLinkToMilkPowder, 
                routerLinkToNutrition, 
                routerLinkToFeminine, 
                routerLinkToCommodity, 
                routerLinkToSnack,
                routerLinkToBabySupplement
            }
		}
    }
</script>

