<template>
	<!-----------------------------------Header wrap---------------------------------->
	<HeaderComponent />
	
	<!--------------------------Company Icon & Search Bar wrap------------------------>
	<SearchComponent />
		
	<!---------------------------------Navigator wrap-------------------------------->
	<NavComponent />

	<div class="title">
		<div class="left">	</div>
		<div class="centerTitle"></div>
		<div class="right">	</div>
	</div>

    <div class="newArrivedproducts" v-for="item in newUpdatedProducts" :key="item._id">
        <div class="newArrivals" @click="routeToSingleProduct(item._id)">
            <img src="item.imgUrl" />
			<div class="content">
				<div class="name">{{item.name}}</div>
			</div>
        </div>
    </div>

</template>

<script>    
	import { reactive, toRefs } from '@vue/reactivity'
	import {useRouter} from 'vue-router'
	import {get} from '../../utils/reqEncap'
    import HeaderComponent from '../../components/HeaderComponent.vue'
    import SearchComponent from '../../components/SearchComponent.vue'
    import NavComponent from '../../components/NavComponent.vue'

    function handleGetNewProductsEvent() {

        const reactiveData = reactive({
            newUpdatedProducts: []
        })
		const route = useRouter()

        async function getNewProducts() {
            // 
            const currentDate = new Date()
            const halfMonthTime = (1000*60*60*24*30)/2
            let halfMonthAgo = currentDate - halfMonthTime
            let halfMonthAgoTime = new Date(halfMonthAgo)

            // 
            const newProductsData = await get('/api/user/product')
            //  reactive
            reactiveData.newUpdatedProducts = newProductsData.data.filter(function(obj){
                // console.log(obj.updatedAt, '---')
                let tempDate = obj.updatedAt
                let tempObjDate = new Date(tempDate)
                return tempObjDate.getTime() > halfMonthAgoTime.getTime()
            })
            // console.log(halfMonthAgoTime.toLocaleDateString(), '---')
        }

		function routeToSingleProduct (singleProductId) {
			localStorage.setItem('singleProductId', singleProductId);
			route.push({name: 'SingleProduct'});
		}
        const {newUpdatedProducts} = toRefs(reactiveData)
        return {newUpdatedProducts, getNewProducts, routeToSingleProduct}
    }

    export default {
        name: 'newProducts',
        components: {HeaderComponent, SearchComponent, NavComponent},
        setup() {
            const {newUpdatedProducts, getNewProducts, routeToSingleProduct} = handleGetNewProductsEvent()
            getNewProducts()

            return {newUpdatedProducts, getNewProducts, routeToSingleProduct}
        }
    }
</script>

<style lang="scss">

    .title{  		
		margin: 0;
		padding: 0;
		margin-left: 18%;
		width: 75%;     /*-Width of 6th div-*/
		height: 80px;
		line-height: 80PX;
		text-align: center;
		display: flex;     /*-Subdivs in row-*/
		flex-flow: nowrap;     /*-No change extra subdivs to next row-*/
		.centerTitle{
			font-size: 150%;
			font-weight: bold;
			width: 12%;
		}
		.left{
			width: 35%;
		}
		.right{

			width: 35%;
		}
	}

	.newArrivedproducts{
		margin: 0 auto;
		padding: 0;
		margin-left: 11%;
		width: 75%;
		// display: inline-block;     /*-Subdivs in row-*/
		// flex-flow: row wrap;     /*-Change extra subdivs to next row-*/
		.newArrivals{
			float: left;
			margin: 1%;
			padding: 0;
			width: 31%;
			height: 390px;
			border: 1px solid grey;
			text-align: center;
			line-height: 350px;
			img{
				width: 80%;
				height: 70%;
				/* images set in mid postion */
				display: block;
				margin: 0 auto;
				border: 1px solid red;
			}
			.content{
				height: 30%;
				.name{
					height: 45%;
					line-height: 50px;
					font-size: 22px;
					border: 1px solid grey;
				}
				.priceSection{
					height: 50%;
				}
			}
		}
		.newArrivals:hover {
			cursor: pointer;
		}
	}

</style>