<template>
	<!-----------------------------------Header wrap---------------------------------->
	<HeaderComponent />
	
	<!--------------------------Company Icon & Search Bar wrap------------------------>
	<SearchComponent />
		
	<!---------------------------------Navigator wrap-------------------------------->
	<NavComponent />

    <!--------------------------------Border line of special area-------------------------------->
	<div class="title">
		<div class="left">	</div>
		<div class="centerTitle"></div>
		<div class="right">	</div>
	</div>	

	<!----------------------------------Products on special------------------------------------->
	<div class="productsOnSpecial" v-for="item in specialsProducts" :key="item._id">
		<div class="special" @click="routeToSingleProduct(item._id)">
			<img src="item.imgUrl" />
			<div class="content">
				<div class="name">{{item.name}}</div>
			</div>
		</div>
	</div>

</template>

<script>

	import { reactive, toRefs } from '@vue/reactivity'
	import {useRouter} from 'vue-router'
	import {get} from '../../utils/reqEncap'
    import HeaderComponent from '../../components/HeaderComponent.vue'
    import SearchComponent from '../../components/SearchComponent.vue'
    import NavComponent from '../../components/NavComponent.vue'

	function handleGetSpecialEvent() {
		const findSpecials = reactive({
			specialsProducts: []
		})
		const route = useRouter()

		async function getSpecialProducts() {
			const specialProductsData = await get('/api/user/product')
			// console.log(specialProductsData.data.special)
			// reactive
			// 
			findSpecials.specialsProducts = specialProductsData.data.filter(
				function(obj){
					return obj.special !== null && obj.special !==0
				}
			)
			// console.log(' --- ', findSpecials.specialsProducts)
		}

		function routeToSingleProduct (singleProductId) {
			localStorage.setItem('singleProductId', singleProductId);
			route.push({name: 'SingleProduct'});
		}
		const {specialsProducts} = toRefs(findSpecials)
		
		return {getSpecialProducts, specialsProducts, routeToSingleProduct}
	}

	export default {
		name: 'SpecialArea',
        components: {HeaderComponent, SearchComponent, NavComponent},
		setup() {
			const {getSpecialProducts, specialsProducts, routeToSingleProduct} = handleGetSpecialEvent()
			getSpecialProducts()

			return {getSpecialProducts, specialsProducts, routeToSingleProduct}
		}
	}
</script>

<style lang="scss">
/*------------------------------------------ Border line --------------------------------------------*/

	/*-Div of border line-*/
	.title{  		
		margin: 0;
		padding: 0;
		margin-left: 18%;
		width: 75%;     /*-Width of 6th div-*/
		height: 80px;
		line-height: 80PX;
		text-align: center;
		display: flex;     /*-Subdivs in row-*/
		flex-flow: nowrap;     /*-No change extra subdivs to next row-*/
		.centerTitle{
			font-size: 150%;
			font-weight: bold;
			width: 12%;
		}
		.left{
			width: 35%;
		}
		.right{

			width: 35%;
		}
	}


/*------------------------------------------- Products on special --------------------------------------*/

	.productsOnSpecial{
		margin: 0 auto;
		padding: 0;
		margin-left: 11%;
		width: 75%;
		// display: inline-block;     /*-Subdivs in row-*/
		// flex-flow: row wrap;     /*-Change extra subdivs to next row-*/
		.special{
			float: left;
			margin: 1%;
			padding: 0;
			width: 31%;
			height: 390px;
			border: 1px solid grey;
			text-align: center;
			line-height: 350px;
			img{
				width: 80%;
				height: 70%;
				/* images set in mid postion */
				display: block;
				margin: 0 auto;
				border: 1px solid red;
			}
			.content{
				height: 30%;
				.name{
					height: 45%;
					line-height: 50px;
					font-size: 22px;
					border: 1px solid grey;
				}
				.priceSection{
					height: 50%;
				}
			}
		}
		.special:hover {
			cursor: pointer;
		}
	}
	


/*-Link to more products-*/
.more{
		width: 10%;
		height: 100%;
}
.more a{
		text-decoration: none;
		line-height:90px;            /*-Set position of the <a> tag-*/
		font-size: 19px;
		
}

.displayArea {
	width: 100%;
	height: 100px;
}



</style>