<template>
    <div class="upload">
        <el-upload 
            :limit="1" 
            :http-request="httpRequest"
            :on-remove="handleRemove" 
            :on-change="onChange" 
            :before-upload="beforeAvatarUpload"
            :on-success="handleUploadSuccess" 
            :file-list="fileList"
        >
            <el-button type="primary"></el-button>
            <span class="successMsg">{{uploadMsg}}</span>
        </el-upload>
    </div>
</template>

<script>
    const OSS = require('ali-oss')
    // import OSS from 'ali-oss'

    const ossClient = new OSS({
        region: 'oss-cn-chengdu',
        accessKeyId: 'LTAI5t8ks8xxsJpVSW8enqUK',
        accessKeySecret: 'sc58v3wqFvjy9D0dkHyvU3f5Y6rKh7',
        bucket: 'moona-test'
    })

    const headers = {
        // Object
        'Cache-Control': 'no-cache', 
        // Object
        // 'Content-Disposition': 'oss_download.txt', 
        // Object
        'Content-Encoding': 'UTF-8', 
        // 
        // 'Expires': 'Wed, 08 Jul 2022 16:57:01 GMT', 
        // Object
        // 'x-oss-storage-class': 'Standard', 
        // Object
        // 'x-oss-object-acl': 'private', 
        // Object
        // 'x-oss-tagging': 'Tag1=1&Tag2=2', 
        // CopyObjectObjecttrueObject
        // 'x-oss-forbid-overwrite': 'true', 
    };

    export default {
        data () {
            return {
                // dialogVisible: false,
                selectedFile: null,
                fileList: [],
                url: '',
                uploadMsg: ''
            }
        },
        methods: {
            onChange(file, fileList) {
                // console.log(file, 'file', fileList, 'Filest');
                this.fileList = fileList
            },
            httpRequest(file) {
                // console.log(file)
                const uploadFileName = file.file.name
                ossClient.put(
                    uploadFileName, file.file, {headers}
                ).then(res => {
                    console.log(res, 'res---header')
                    if(res) {
                        this.url = res.url
                        console.log(this.url, '')
                        this.uploadMsg = ''
                    }
                }).catch(err => {
                    console.log(err, 'err')
                })
                // this.$emit('Picture url', this.url)
            },
            handleUploadSuccess(response, file, fileList) {
                console.log(response, file, fileList, 'response, file, fileList')
            },
            handleRemove(e, fileList) {
                this.fileList = fileList;
            },
            beforeAvatarUpload(file) {
                // 
                const isLt2M = file.size / 1024 / 1024 < 10;
                if (!isLt2M) {
                    this.$message.error(' 10M !');
                }
                return isLt2M;
            },
        }
    }
</script>

<style lang="scss">
    .upload {
        margin-left: 10%;
        width: 80%;
        .el-upload {
            width: 100%;
            padding: 0;
            margin-left: 0px;
            .el-button {
                width: 40%;
                text-align: center;
                margin-left: 0px;
                span {
                    margin-left: -10px;
                }
            }
            .successMsg {
                margin-left: 20px;
                text-align: left;
                width: 50%;
            }
        }
    }
</style>