<template>
	<div>	
		<div class="subSearchWrap">
			<input type="text">
			<button>
				
			</button>
		</div>	
		<table>
			<caption></caption>
			<thead v-if="checkLoginData.isAdmin">
				<th></th>
				<th></th>
				<th>VIP</th>
				<th></th>
				<th></th>
			</thead>
			<tbody v-if="checkLoginData.isAdmin">
				<tr v-for="item in usersAddrList" :key="item._id">
					<td>{{item.username}}</td>
					<td>{{item.province}}
						{{item.city}}
						{{item.suburb}}
						{{item.street}}
						{{item.houseNum}}
						/ {{item.name}}
						/ {{item.phone}}</td>
					<td>{{item.vip}}</td>
					<td>
						<!--1.  - updateUserAddr -->
						<button @click="updateUserAddr(item._id, item.username)"></button>
					</td>
					<td>
						<button @click="deleteUserAddr(item._id)"></button>						
					</td>
				</tr>
			</tbody>
		</table>
	</div>
	<!-- 1. Parent compnt passes dynamic data to child compnt, need to bind the reactive data  -->
	<!-- 1.1 Child compnt will get the data by using the name of the data - 'userID' -->
	<MngmntUpdateUser v-if="updateData.showUpdatePanel" :userID="updateData.userID" :userName="updateData.userName" />
</template>


<script>
	import {reactive, toRefs} from 'vue'
	import { get, deleteItem } from '../../utils/reqEncap'
	import {checkIsLogin} from '../../midwares/checkIsLogin'
	import MngmntUpdateUser from '../../components/MngmntUpdateUser.vue'

	function handleUsersAddr() {
		// Create an array list in a reactive data for users' addresses receiving
		const usersData = reactive({
			usersAddrList: []
		})
		// Receive list of addresses by 'addrList'
		async function getAddrList() {
			const addrList = await get('/api/user/address/usersList')
			usersData.usersAddrList = addrList.data
			// console.log(usersData.usersAddrList)
		}
		// Destruct reactive data to get array in the data
		const {usersAddrList} = toRefs(usersData)
		return {getAddrList, usersAddrList}
	}

	function handleUserDelete() {
		async function deleteUserAddr(id) {
			try {
					// console.log(id,'--id')
					const deleted = await deleteItem(`/api/user/address/${id}`)
					// console.log(deleted, '---')	
			} catch (ex) {
				throw new Error(ex)
			}
		}	

		return {deleteUserAddr}
	}

	function handleAddrUpdate() {
		const updateData = reactive({
			showUpdatePanel: false,
			userID: '',
			userName: ''
		})
		// 2.  - updateUserAddrv-for
		function updateUserAddr(id, name) {
			updateData.userID = id
			console.log(name,'--name')
			updateData.userName = name
			updateData.showUpdatePanel = !updateData.showUpdatePanel
		}
		return {updateData, updateUserAddr}
	}

	export default {
		name: 'AdminCustomers',
		components: { MngmntUpdateUser },
		setup () {
			// Get all users info
			const {getAddrList, usersAddrList} = handleUsersAddr()
			const {checkLoginData} = checkIsLogin()
			// Update selected user's address
			const {updateData, updateUserAddr} = handleAddrUpdate()
			// Delete selected user's address
			const {deleteUserAddr} = handleUserDelete()
			if(checkLoginData.isAdmin){
				getAddrList()
			}			
			return {
				getAddrList, 
				usersAddrList, 
				checkLoginData, 
				updateData, 
				updateUserAddr, 
				deleteUserAddr
			}
		}
	}
</script>

<style lang='scss'>

	table {
		margin: 0 auto;
		width: 100%;
		font-size: 20px;
		padding: 10px;
		caption {
			width: 100%;
			font-size: 30px;
		}
		thead th {
			border-top: 1px grey solid;
			border-bottom: 1px grey solid;
			border-right: 1px grey solid;
			padding: 20px;
			text-align: center;
		}

		tbody tr {
			td{
				border-bottom: 1px grey solid;
				border-right: 1px grey solid;
				height: 10px;
				text-align: center;
				line-height: 10px;
			}
			#tdOne{
				padding: 0 10px;
			}
		}
	}

	.subSearchWrap {
		width: 60%;
		height: 50px;
		margin: 50px auto;
		padding: 0;
		font-size: 22px;
		input {
			width: 85%;
			height:45px;
		}
		button {
			width: 8%;
			height: 50px;
			margin-left: 10px;
		}
	}

</style>