<template>
	<HeaderComponent />

<div>
	<SearchComponent  @childInfo="childInfo" />
</div>
	
	<NavComponent />
	<div class="searchResultsWrap">	
		<table >
			<caption></caption>
			<thead>
				<th></th>	
				<th></th>
				<th></th>		
				<th></th>
				<th></th>		
			</thead>
			<tbody v-if="hasSearchResults">
				<tr class="productsDetails" v-for="item in receivingData" :key="item._id" @click="routeToSingleProduct(item._id)">
					<td>{{item.name}}</td>
					<td>{{item.category}}</td>
					<td>{{item.brand}}</td>
					<td>{{item.quantity}}</td>
					<td>{{item.price}}</td>
				</tr>
			</tbody>
			<tbody v-else> 
				<tr>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
				</tr>
			</tbody>
		</table>
	</div>
	<ToastComponent v-if="toastData.showToast" :message="toastData.toastMessage" />
</template>

<script>
	import { reactive } from '@vue/reactivity'
	import {toRefs, watchEffect} from 'vue'
	import { useRoute, useRouter } from 'vue-router'
	import {get} from '../../utils/reqEncap'
	import HeaderComponent from '../../components/HeaderComponent.vue'
	import SearchComponent from '../../components/SearchComponent.vue'
	import NavComponent from '../../components/NavComponent.vue'
	import ToastComponent, {ToastParts} from '../../components/ToastComponent.vue'

	function handleSearchResult(settingFailureToast) {
		const reactiveData = reactive({
			receivingInfo: '',
			receivingData: [],
			hasSearchResults: false
		})
		const route = useRouter()
		

		// Receiving information sent from search function
		let paramInfo = ''
		const {info} = useRoute().query
		// let infoFromSearch = ''

		async function childInfo(val) {
			// console.log(info, '---info')
			if(val == undefined || val == '' || val == null) {
				reactiveData.receivingInfo = info
			}else {
				reactiveData.receivingInfo = val
			}
			// console.log(reactiveData.receivingInfo,'---1')
			// paramInfo
			//  reactiveData.receivingInfoemit, paramInfo
			if(info == reactiveData.receivingInfo || info == reactiveData.receivingInfo || info == reactiveData.receivingInfo) {
				// console.log(info.toLowerCase(), '---test')
				paramInfo = info.toLowerCase()
			}else{
				paramInfo = reactiveData.receivingInfo.toLowerCase()
			}
		
			// console.log(paramInfo, '---paramInfo')
			const tempArr = []

			// console.log(tempInput, '---')
			if(paramInfo== '' || paramInfo == null){
				settingFailureToast('')
			}else {
				try{
					const productsInfo = await get('/api/user/product')
					for(const item of productsInfo.data) {
						if(
							item.name.toLowerCase().includes(paramInfo) || 
							item.category.toLowerCase().includes(paramInfo) || 
							item.brand.toLowerCase().includes(paramInfo)
						)
						{
							// console.log(item.name, '---itemname')
							tempArr.push(item)

						}else {
							tempArr.push()
						}
					}
					// console.log(tempArr, '---tempArr')
					
					reactiveData.receivingData = tempArr
					// console.log(reactiveData.receivingData, '---')
				}catch(ex) {
					throw new Error(ex)
				}
				
					if(reactiveData.receivingData.length != 0) {
						reactiveData.receivingData = tempArr
						reactiveData.hasSearchResults = true
					}else {
						reactiveData.hasSearchResults = false
					}
					// console.log(reactiveData.hasSearchResults, '---reactiveData.hasSearchResults')
			}
		}

		function routeToSingleProduct (singleProductId) {
			localStorage.setItem('singleProductId', singleProductId);
			route.push({name: 'SingleProduct'});
		}

		const {receivingData, hasSearchResults} = toRefs(reactiveData)
		

		return {receivingData, hasSearchResults, childInfo, routeToSingleProduct}
	}

	export default {
		name: 'searchResults',
		components: { HeaderComponent, SearchComponent, NavComponent, ToastComponent },
		setup() {
			const {toastData, settingFailureToast} = ToastParts()
			const {receivingData, hasSearchResults, childInfo, routeToSingleProduct} = handleSearchResult(settingFailureToast)
			childInfo()

			return { toastData, hasSearchResults, receivingData, childInfo, routeToSingleProduct}
		},
		methods: {
			handleSearch(msg) {
				console.log(msg,'---msg')
			}
		},
	}
</script>

<style lang="scss">

	.searchResultsWrap {
		margin: 0 auto;
		margin-top: 10px;
		width: 90%;
		table {
				margin: 10px auto;
				// padding: 0 auto;
			caption {
				margin: 10px auto;
				font-size: 30px;
				font-weight: bold;
			}
			thead {
				width: 90%;
				th {
					width: 10%;
					font-size: 20px;
					border-bottom: 1px grey solid;
				}
			}
			tbody {
				tr {
					td {
						text-align: center;
						border-bottom: 1px rgb(223, 223, 223) solid;
						height: 30px;
					}
				}
				tr:hover{
					background-color: #cfe0fe;
					cursor: pointer;
				}
			}
		}
	}


</style>