<template>
    <!-----------------------------------Border line of gallery-------------------------------->
	<div class="borderLine">
			<div class="left">
				<img alt="" src="img/border line.png">
			</div>
			<div class="centerTitle"></div>
			<div class="right">
				<img alt="" src="img/border line.png">
			</div>
			<div class="more">
				<a href="" >+</a>
			</div>
	</div>	
	
		
	<!-------------------------------------Milk powder gallery--------------------------------------->
	
	<div class="milkPowderProductsGallery">		
		<div class="milkPowders" v-for="item in mostSaleMilkPowders.slice(0,10)" :key="item._id">
			<div class="picture">
				<img src="" alt="">
			</div>
			<div class="content">
				<div class="name">{{item.name}}</div>
				<div class="priceSection">{{item.price}}</div>
			</div>
		</div>
	</div>
	

</template>

<script>
	import {reactive, toRefs} from '@vue/reactivity'
	import {get} from '../../utils/reqEncap'
	
	function handleMilkPowderDisplayEvent() {
		const reactiveData = reactive({
			mostSaleMilkPowders: []
		})

		async function getMostSalesMilkPowders() {
			const getData = await get('/api/user/product')
			// console.log(getData.data, '---')
			reactiveData.mostSaleMilkPowders = getData.data.filter(function(obj){
				return obj.category == '' && obj.quantity >= 100
			})
			
		}
		const {mostSaleMilkPowders} = toRefs(reactiveData)

		return {mostSaleMilkPowders, getMostSalesMilkPowders}
	}

	export default {
		name: 'HomeMilkPowderDisplay',
		setup() {
			const {mostSaleMilkPowders, getMostSalesMilkPowders} = handleMilkPowderDisplayEvent()
			getMostSalesMilkPowders()
		
			return {mostSaleMilkPowders, getMostSalesMilkPowders}
		}
	}
</script>

<style lang="scss">
	.milkPowderProductsGallery{
		margin: 0 auto;
		padding: 0;
		margin-left: 10%;
		width: 80%;
		// display: inline-block;     /*-Subdivs in row-*/
		// flex-flow: row wrap;     /*-Change extra subdivs to next row-*/
		.milkPowders {
			float: left;
			margin: 1%;
			padding: 0;
			width: 17%;
			height: 290px;
			border: 1px solid grey;
			text-align: center;
			line-height: 350px;
			.picture {
				width: 80%;
				height: 70%;
				/* images set in mid postion */
				display: block;
				margin: 0 auto;
				background-color: #cfe0fe;
				img {
					width: 100%;
					height: 100%;
				}
			}
			.content {
				height: 30%;
				.name{
					height: 45%;
					line-height: 300%;
					font-size: 100%;
					font-weight: bold;
					border: 1px solid grey;
					color: #3a1e9d;
				}
				.priceSection{
						height: 50%;
						// border: none;
						line-height: 300%;
						font-size: 100%;
						font-weight: bold;
						color: rgb(190, 3, 3);
				}
			}
		}
		.milkPowders:hover {
			cursor: pointer;
		}
	}
</style>