<template>
	<!-----------------------------------Header wrap---------------------------------->
	<HeaderComponent />
	
	<!--------------------------Company Icon & Search Bar wrap------------------------>
	<SearchComponent />
		
	<!---------------------------------Navigator wrap-------------------------------->
	<NavComponent />

	<!----------------------------------------Products link & Customer register wrap------------------------------------------->
	<!--------------------------------Products catalog--------------------------->
	<div class="productsArea">
		<SideProductsCatlogComponent />				
		<!-------------------Scrolling Image----------------->
		<div class="registerWrap">		
			<div id="title">
				<p></p>
			</div>			
			<div id="register_table">
				<div id="reg">
					<table align="center">
						<tr>
							<td></td>
							<td>
								<input type="text" name="username" placeholder="" v-model="data.username" />
							</td>
						</tr>						         
						<tr>
							<td></td>
							<td>
								<input 
									type="password" name="pswd"  
									placeholder="" 
									v-model="data.password" 
									autocomplete="new-password"
								/>
							</td>
						</tr>
						<tr>
							<td></td>
							<td>
								<input 
									type="password" 
									name="confirmPswd"
									placeholder=""
									autocomplete="new-password"
									v-model="data.pswdConfirm"
								/>
							</td>
						</tr>
						<tr>
							<td></td>
							<td><div id="reg_button" @click="registerEvent"></div></td>
						</tr>
					</table>
				</div>
			</div>
		</div>	
	</div>
		<ToastComponent v-if="toastData.showToast" :message="toastData.toastMessage"/>
</template>

<script>
	import { reactive } from '@vue/reactivity'
	import { useRouter } from 'vue-router';
	import { post } from '../../utils/reqEncap'
	import HeaderComponent from '../../components/HeaderComponent.vue'
	import ToastComponent, {ToastParts} from '../../components/ToastComponent.vue'
	import SearchComponent from '../../components/SearchComponent.vue'
	import NavComponent from '../../components/NavComponent.vue'
	import SideProductsCatlogComponent from '@/components/SideProductsCatlogComponent.vue'

	// Setup register event
	function handleRegisterEvent(settingSuccessRegisterToast, settingFailureToast) {

		const router = useRouter()
		// 'data' needs to be exports for use
		const data = reactive({
			username: '',
			password: '',
			pswdConfirm: ''
		})
		async function registerEvent() {
			// Check if password confirm inputs matches password inputs
			if(data.password === '' || data.password === null){
				settingFailureToast('')
			}else if(data.password !== data.pswdConfirm){
				settingFailureToast('')
			}else if(data.password === data.pswdConfirm){
				// Ready to send post request if password confirmation is ok
				try {
					const registerResult = await post(
						'/api/user/register', {
							username: data.username,
							password: data.password
						}
					)
					if(registerResult?.errno === 0 ){
						settingSuccessRegisterToast('')
					}else{
						settingFailureToast('')
					}
				}catch(ex) {
					settingFailureToast('')
				}					
			}
		}
		return {data, registerEvent}
	}

	// Combine & simplify processes of registering
	export default {
		name: 'RegisterPage',
		components: { HeaderComponent, SearchComponent, NavComponent, SideProductsCatlogComponent, ToastComponent },
		setup () {
			const { toastData, settingSuccessRegisterToast, settingFailureToast } = ToastParts()
			const { data, registerEvent } = handleRegisterEvent(settingSuccessRegisterToast, settingFailureToast)

			
			return {registerEvent, data, toastData}
		}
	}
</script>

<style scoped>
	/*--------Register page---------*/
	.registerWrap{
			margin-left: 10%;
			margin-top: 2px;
			width: 61%;
			height: 430px;              
			overflow: hidden;

	}

	/*------------Register-----------*/
	.registerWrap #title{
			margin: 0 auto;
			font-size: 35px;
			width: 100%;
			height: 90px;
			font-weight: bold;
			text-align: center;

	}


	.registerWrap #register_table{
			position: relative;
			margin-top: -20px;
			width:100%;
			height: 380px;
			background-color: #f3f3f3;
	}

	.registerWrap #register_table #reg{
			position: absolute;
			width: 50%;
			left: 21%;

	}

	.registerWrap #register_table #reg #reg_button{
			background-color: #9ec1fc;
			color: #3a1e9d;
			text-align: center;
	}


	.registerWrap #register_table #reg #reg_button:hover{
			color: #5a5ffa;
			cursor: pointer;
	}

	.registerWrap #register_table #reg form{
		
			font-size: 22px;
			font-weight: bold;

	}

	.registerWrap #register_table #reg table{
			border-spacing: 2px 10px;
	}

	.registerWrap #register_table #reg input{
			width: 215px;
			height: 26px;
			margin: 3px 0px 0px 10px;
			border: 0px;
			font-size: 22px;
	}
</style>