<template>
	<!-------------------Header wrap-------------------->
	<HeaderComponent />
	
	<!-----------------Company Icon & Search Bar wrap---------------->
	<SearchComponent />
	
	<!----------------Navigator wrap----------------->
	<NavComponent />

	<div class="productsArea">
		<SideProductsCatalogComponent />
		<div class="milkPowders">
			<div 
				class="productsOfMilkPowder" 
				v-for="item in milkPowders" 
				:key="item._id"
			>
				<div class="singleProduct" @click="routeToSingleProduct(item._id)">
					<img src="item.imgUrl" />
					<div class="content">
						<div class="name">{{item.name}}</div>
						<div class="priceSection">{{item.price}}</div>
					</div>
				</div>
			</div>
		</div>
	</div>

	<!-------------Milk Powders------------->
    <!-- Title of top tabs -->
	<!-- <ul class="topTabs" >
		<li v-for="(item, index) in tags" :key="item.id" class="titleTags">
			<p :class="{isActive:index==active}" @click="handleTagsEvent(index)">{{item}}</p>
		</li>
	</ul>	 -->

    <!-- Brands & products of all milkpowders -->
    <!-- <ul class="brands">			

	</ul>	 -->
</template>

<script>

	import {reactive, toRefs} from '@vue/reactivity'
	import {useRouter} from 'vue-router'
	import {get} from '../../../utils/reqEncap'
    import HeaderComponent from '../../../components/HeaderComponent.vue'
    import SearchComponent from '../../../components/SearchComponent.vue'
    import NavComponent from '../../../components/NavComponent.vue'
	import SideProductsCatalogComponent from '../../../components/SideProductsCatlogComponent.vue'
	
	function handleGetMilkPowderEvent() {
		const reactiveData = reactive({
			milkPowders: []
		})
		const route = useRouter()

		async function getMilkPowders() {
			const milkPowderData = await get('/api/user/product')
			reactiveData.milkPowders = milkPowderData.data.filter(
				function(obj) {
					return obj.category == ''
				}
			)
			// console.log(reactiveData.milkPowders,'---1')
		}

		function routeToSingleProduct (singleProductId) {
			localStorage.setItem('singleProductId', singleProductId);
			route.push({name: 'SingleProduct'});
		}
		// console.log(reactiveData.milkPowders,'---2')
		const {milkPowders} = toRefs(reactiveData)

		return {milkPowders, getMilkPowders, routeToSingleProduct}
	}



    export default {
        name: 'MilkPowders',
        components: { HeaderComponent, SearchComponent, NavComponent, SideProductsCatalogComponent },
		setup() {
			const {milkPowders, getMilkPowders, routeToSingleProduct} = handleGetMilkPowderEvent();
			getMilkPowders();

			return {milkPowders, getMilkPowders, routeToSingleProduct};
		}
    }
</script>

<style lang="scss">
	.milkPowders {
		border: 1px solid grey;
		width: 65%;
		min-height: 100px;
		margin-left: 20px;
		.productsOfMilkPowder{
			float: left;
			margin: 2%;
			padding: 0;
			width: 19%;
			height: 220px;
			border: 1px solid grey;
			text-align: center;
			line-height: 350px;
			.singleProduct{
				width: 100%;
				height: 100%;
				img{
					padding: 0px;
					width: 80%;
					height: 70%;
					/* images set in mid postion */
					display: block;
					margin: 0 auto;
					background-color: grey;
					overflow: hidden;
				}
				.content{
					height: 30%;
					.name{
						height: 45%;
						line-height: 200%;
						font-size: 80%;
						border: 1px solid grey;
						color: #007572;
					}
					.priceSection{
						height: 50%;
						// border: none;
						line-height: 200%;
						font-size: 100%;
						font-weight: bold;
						color: rgb(190, 3, 3);
					}
				}
			}
		}
	}

</style>