<template>
	<!-----------------------------------Header wrap---------------------------------->
	<HeaderComponent />
	
	<!--------------------------Company Icon & Search Bar wrap------------------------>
	<SearchComponent />
	
	<!---------------------------------Navigator wrap-------------------------------->
	<NavComponent />
		
	
	<!----------------------------------------Products link & login wrap------------------------------------------->
	<!--------------------------------Products catalog--------------------------->
	<div class="productsArea">

		<SideProductsCatlogComponent />
		<!------------------------------Login----------------------------->
		<div class="login_wrap">
			<div id="login_title">
				<p></p>
			</div>
			<div id="login_table">
				<div id="log">
					<table align="center">
						<tr>
							<td width="120"></td>
							<td>
								<input 
									type="text" 
									name="login_name" 
									class="inp" 
									placeholder="" 
									v-model="data.username"
								/>
							</td>
						</tr>
						<tr>
							<td></td>
							<td>
								<input 
									type="password" 
									name="login_pswd" 
									class="inp" 
									placeholder="" 
									v-model="data.password"
									autocomplete="new-password"
								/>
							</td>
						</tr>
						<tr>
							<td></td>
							<td colspan="2"><div class="login_button" @click="loginEvent">{{loginStatus}}</div></td>
						</tr>
					</table>
				</div>
			</div>
		</div>
			
	</div>
	<ToastComponent v-if="toastData.showToast" :message="toastData.toastMessage" />
</template>

<script>
	import { reactive } from '@vue/reactivity'
	import {useRouter} from 'vue-router'
	import { post } from '../../utils/reqEncap'
	import HeaderComponent from '../../components/HeaderComponent.vue'
	import SearchComponent from '../../components/SearchComponent.vue'
	import NavComponent from '../../components/NavComponent.vue'
	// importing file has no '{}', importing function has '{}'
	import ToastComponent, {ToastParts} from '../../components/ToastComponent.vue'
	import SideProductsCatlogComponent from '../../components/SideProductsCatlogComponent.vue'

	// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
	// Before encapsulation the [Content-Type] is set in default
	// After encapsulation the default value is set off
	// axios.defaults.headers.post['Content-Type'] = 'application/json'

	//Set login event function
	function handleLoginEvent(settingSuccessLoginToast, settingFailureToast) {
		
		// const router = useRouter()
		// data would receive inputs from the web page, then pass to post method for request sending
		const data = reactive ({
			username: '',
			password: ''
		})
		// Handling @click login event button
		async function loginEvent () {			
			try {
				const loginResult = await post(
					'/api/user/login', 
					{
						username: data.username,
						password: data.password
					}
				)
				// console.log(loginResult)
				if(loginResult?.errno === 0 ){
					// alert(data.username)
					// sessionStorage.setItem('username', data.username)
					localStorage.setItem('username', data.username)
					settingSuccessLoginToast('')
				}
				// Check error if login failed
				else{
					settingFailureToast('')
				}
				// Check error if request failed to send
			}catch(e) {
				settingFailureToast('')
			}
		}

		return { data, loginEvent}
	}

	//Combine & simplify processes of login 
	export default {
		name: 'LoginPage',
		components: {HeaderComponent, SearchComponent, NavComponent, SideProductsCatlogComponent, ToastComponent},
		setup() {
			
			let loginStatus = '...'
			const {toastData, settingSuccessLoginToast, settingFailureToast} = ToastParts()
			const {data, loginEvent} = handleLoginEvent(settingSuccessLoginToast, settingFailureToast)


			// const router = useRouter()
			// const routerLinkToCart = () => {
			// 	router.push({name: 'Cart'})
			// }
			// const routerLinkToRegister = () => {
			// 	router.push({name: 'Register'})
			// }
			// const routerLinkToHomepage = () => {
			// 	router.push({name: 'Home'})
			// }
			return { 
				loginEvent, 
				loginStatus,
				data,
				toastData
			}
		}
	}
</script>

<style scoped>

    /*------------Login-----------*/
    .login_wrap{
            margin-left: 10%;
            margin-top: 1px;
            width: 800px;
            height: 430px;
            overflow: hidden;

    }


    #login_title {
            margin: 0 auto;
            font-size: 35px;
            width: 100%;
            height: 90px;
            font-weight: bold;
            text-align: center;

    }

    #login_table {
            position: relative;
            margin: 0 auto;
            width:100%;
            height: 380px;
            background-color: #f3f3f3;
    }


    #log {
            position: absolute;
            width: 50%;
            height: 190px;
            left: 21%;
            top: 10%;
    }



    #log table{
            font-size: 22px;
            font-weight: bold;
            border-spacing: 2px 10px;
    }

    #log input{
            width: 215px;
            height: 26px;
            margin: 3px 0px 0px 10px;
            border: 0px;
            font-size: 22px;
    }

    #log .login_button{
            background-color: #9ec1fc;
            color: #3a1e9d;
            width: 215px;
            height: 26px;
            margin: 3px 0px 0px 10px;
            border: 0px;
            font-size: 22px;
            text-align: center;
    }

    #log .login_button:hover{
            color: #5a5ffa;
            cursor: pointer;
    }


</style>